.container {
    padding-left: 25px;
    padding-right:25px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    @include mq(medium) {
        width: 95%;

    }
    @include mq(large) {
        width: 90%;
    }
    @include mq(x-large) {
        width: $break--x-lrg;
    }
}